<template>
  <div id="app">
    <div class="fixHead">
      <div class="headBg">
        <img class="logo" alt="fma" src="../static/page1/LOGO.png" />
        <img class="nav" @click="clickNav" alt="fma" src="../static/page1/nav.png" />
        <div class="navDiv" v-show="navShow">
          <router-link to="/">
            <div class="navDiv1">
              <img class="img1" alt="fma" src="../static/homePageS.png" />
              <span>首页</span>
            </div>
          </router-link>
          <router-link to="/page2">
            <div class="navDiv2">
              <img class="img2" alt="fma" src="../static/aboutS.png" />
              <span>关于我们</span>
            </div>
          </router-link>
        </div>
      </div>
      <div class="navBar" v-bind:class="{ active: isActive }">
        <a href="#gongsi" class="navItem">公司简介</a>
        <a href="#chuangshi" class="navItem">创始团队</a>
        <a href="#shijian" class="navItem">大事件</a>
        <a href="#lianxi" class="navItem">联系我们</a>
        <a href="#jiaru" class="navItem">加入我们</a>
      </div>
    </div>
    <router-view />
  </div>
</template>

<script>
/* eslint-disable */
import page1 from "@/views/page1.vue";
import page2 from "@/views/page2.vue";
export default {
  name: "app",
  components: {
    page1,
    page2
  },
  mounted() {
    //监控浏览器地址，防止直接输入地址访问，就监控不到路由变化了
    if (window.location.pathname == "/page2") {
      this.isActive = false;
    } else {
      this.isActive = true;
    }
  },
  computed: {},
  watch: {
    //监听路由变化
    "$route.path": function(newVal, oldVal) {
      this.navShow = false;
      if (newVal == "/page2") {
        this.isActive = false;
      } else {
        this.isActive = true;
      }
      window.scrollTo(0,0);
    }
  },
  methods: {
    clickNav() {
      this.navShow = !this.navShow;
    }
  },
  data() {
    return {
      navShow: false,
      isActive: true,
      active: 2,
      routers: this.$router.options.routes,
      show: false,
      menu: [],
      currentRouter: {}
    };
  }
};
</script>
