        this.form.company === "" ||
        this.form.email === "" ||
        this.form.phone === "" ||
        this.form.position === ""
      ) {
        this.$toast({
          type: "fail",
          position: "top",
          message: "以下所有内容为必填项，不能为空"
        });
      } else {
        let value = this.form.phone;
        if (value && !/^((0\d{2,3}-\d{7,8})|(1[3584]\d{9}))$/.test(value)) {
          this.$toast({
            type: "fail",
            message: "电话号码不符合规范"
          });
          return;
        }

        let temp = /^[\w.\-]+@(?:[a-z0-9]+(?:-[a-z0-9]+)*\.)+[a-z]{2,3}$/;
        let tempOne = /^[A-Za-zd]+([-_.][A-Za-zd]+)*@([A-Za-zd]+[-.])+[A-Za-zd]{2,5}$/;
        value = this.form.email;
        if (value && !temp.test(value)) {
          this.$toast({
            type: "fail",
            message: "邮箱地址不符合规范"
          });
          return;
        }

        let params = {
          userName: this.form.name,
          userEmail: this.form.email,
          userCompany: this.form.company,
          userPhone: this.form.phone,
          userJob: this.form.position,
          userType: 1
        };
        addUser(params).then(res => {
          if (res.errcode === 0) {
            this.$toast({
              type: "success",
              message:
                "提交成功，我们会在一个工作日内与您联系，请保持手机畅通！"
            });
          } else {
            this.$toast({
